import React from "react"
import { MyBase } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"
import { FlowClickToSelectInput, KMore } from "src/helpers/Flows/TableCell"
const WrapCell = styled.div`
  display: flex;
  & > textarea {
    border: 0;
    outline: none;
    resize: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100%;
    height: 100%;
    background: transparent;
    &:focus {
      background: var(--color-bg-focus);
    }
    &[rows] {
      position: relative;
    }
  }
`

export default function MTCTextarea(props) {
  const ctc = useConfigTableCell({ props })
  // console.log("render MTCTextarea:", props, ctc)
  return (
    <WrapCell
      className={ctc.classNameWrap("mtc-textarea", { scrwhhover: true })}
      style={ctc.styleWrap()}
    >
      <MyBase
        type="textarea"
        value={ctc.valueCell}
        onChange={v => {
          ctc.setValueCell(v)
        }}
        onBlur={v => {
          ctc.updateFieldData(v)
        }}
        attrs={{
          rows: ctc.moreObj[KMore.rows] || null,
          disabled: ctc.canEdit === true ? false : true,
          onClick: ev => {
            FlowClickToSelectInput({ more: ctc.more, ev })
          },
        }}
      />
      {ctc.renderTopRight([], { ctc })}
    </WrapCell>
  )
}
