/*eslint-disable @typescript-eslint/no-unused-vars */
import {
  HOptions,
  HUtils,
  MyFormControlHelpers,
  MyTableCellHelpers,
  MyUI,
  MyLoading,
  MyTableCell,
  OneTableCell,
} from "@macashipo/mlib"
import cls from "classnames"
import React, { useState } from "react"
import { MyModal } from "src/core/components"
import { fnListMyPage } from "src/helpers/fnList"
import apiWithConfigApi from "src/services/apiConfigApi"
import V from "src/helpers/V"
import { fnListOfTableCell, helpersOfTableCell } from "./fnListOfTableCell"
import useForceUpdate from "./useForceUpdate"
import {
  FlowCheckApiShowModal,
  FlowParseResponseAndNeedReload,
} from "src/helpers/Flows/TableCell"
const kfnRequestUpdate = "fnRequestUpdate"
const kfnRequestCustom = "fnRequestCustom"

function ModalContentFiles(props) {
  const { ctc, more } = props
  let _id = ctc.row.Id
  let _screenCode = fnListOfTableCell.fnGetScreenCode(ctc)
  let _configFileFromMore = {}
  if (more) {
    if (more.fID && ctc.row[more.fID]) {
      _id = ctc.row[more.fID]
    }
    _configFileFromMore = more.configFile || more.file || {}
  }
  const configFile = {
    needRequestPhotos: true,
    pasteAsFile: true,
    showUploadFromGGDrive: false,
    ..._configFileFromMore,
  }
  const requestData = {
    ScreenCode: _screenCode,
    ProjectId: "",
    Id: _id,
    FieldName: ctc.fieldName,
  }
  // console.warn("render filemanager:", requestData, ctc.fnList)
  return (
    <div>
      <MyUI
        type="file_manager"
        requestData={requestData}
        fnList={props.fnList}
        configFile={configFile}
        canUpload={props.canUpload}
      />
    </div>
  )
}
const ATCHelper = {
  //can chuyen sang Flow
  getCanEdit: ({ fieldName, extra, row, more }) => {
    let _extra = extra || {}
    let _more = more
    let _canEdit = false
    if (typeof _extra.CanEdit === "boolean") {
      _canEdit = _extra.CanEdit
    }
    //check more.fCanEdit more.canEditF
    if (_canEdit == true) {
      if (_more.fCanEdit == null && _more.canEditF != null) {
        _more.fCanEdit = _more.canEditF
      }
      if (typeof _more.fCanEdit === "boolean") {
        _canEdit = _more.fCanEdit
      } else if (typeof _more.fCanEdit === "string") {
        if (_more.fCanEdit.length > 0) {
          if (row[_more.fCanEdit] != null) {
            _canEdit = row[_more.fCanEdit]
          } else if (_more.fCanEdit.indexOf(",") > -1) {
            let _arrcanEditF = _more.fCanEdit.split(",")
            if (_arrcanEditF && _arrcanEditF.length > 0) {
              for (let f of _arrcanEditF) {
                if (row[f] == false) {
                  //only need one cannot edit -> cannot edit
                  _canEdit = false
                  break
                }
              }
            }
          }
        }
      }
    }
    //check more.lockF more.fLock
    if (_more.lockF != null || _more.fLock != null) {
      let _fLock = _more.lockF || _more.fLock
      if (typeof _fLock == "boolean") {
        _canEdit = !_fLock
      } else if (typeof _fLock == "string") {
        if (_fLock.length > 0) {
          if (row[_fLock] != null) {
            _canEdit = !row[_fLock]
          } else if (_fLock.indexOf(",") > -1) {
            let _arrLock = _fLock.split(",")
            if (_arrLock && _arrLock.length > 0) {
              for (let f of _arrLock) {
                if (row[f] == true) {
                  //only need one lock edit -> cannot edit
                  _canEdit = false
                  break
                }
              }
            }
          }
        }
      }
    }
    return _canEdit
  },
}

var countForceUpdate = 0
// var countForceUpdateMax = 1000
function useConfigTableCell({ props } = {}) {
  countForceUpdate++
  const [valueFU, setValueFU] = useState(0) // integer state
  const [valueCell, setValueCell] = useState(
    props.cell === null || props.cell === undefined ? "" : props.cell
  ) // integer state
  const { cell, extra } = props
  const { fieldName } = extra
  const [isUpdating, setIsUpdating] = useState(false)
  const [isFetching, setIsFetching] = useState(false)
  const refOneTableCell = React.useRef(new OneTableCell(props))
  const oneTableCell = refOneTableCell.current
  // update row and extra to oneTableCell, may be it will change so need update
  // oneTableCell.update(props.row, extra)
  oneTableCell.row = props.row
  oneTableCell.extra = extra
  if (props.custom) {
    if (oneTableCell["_updateCustom"] !== true) {
      oneTableCell.more = { ...props.custom.more, ...oneTableCell.more }
      oneTableCell["_updateCustom"] = true
      // console.warn("new more:", oneTableCell.more)
    }
  }

  const row = oneTableCell.row
  const refFnList = React.useRef(
    fnListOfTableCell.init({ oneTableCell, row, extra, cell })
  )
  const fnList = refFnList.current
  if (oneTableCell._onePage == null) {
    oneTableCell.updateOnePage(fnListMyPage.fnGetOnePage(fnList))
  }
  const more = oneTableCell.more
  // const forceUpdate = () => {
  //   console.warn("forceUpdate", countForceUpdate)
  //   setValueFU(valueFU + 1)
  // }
  const forceUpdate = useForceUpdate()
  //chua hieu vi sao 2 forceUpdate se hoat dong khac nhau
  //setValueFU nhieu luc ko hoat dong

  const updateValueCellFromRow = ({ cbWhenEqual } = {}) => {
    let _value = row[fieldName]
    console.log("updateValueCellFromRow:", _value)
    if (valueCell != _value) {
      setValueCell(_value === null || _value === undefined ? "" : _value)
    } else {
      console.log("call cbWhenEqual")
      if (cbWhenEqual) {
        cbWhenEqual()
      }
    }
  }

  //parse
  if (row.IsLocked === true) {
    oneTableCell.canEdit = false
  } else if (
    oneTableCell.canEdit == null ||
    oneTableCell.canEdit === true ||
    oneTableCell.canEdit === false
  ) {
    oneTableCell.canEdit = ATCHelper.getCanEdit({
      extra: oneTableCell.extra,
      more,
      row,
    })
  }

  React.useEffect(() => {
    // console.log(
    //   "props.cell change:",
    //   oneTableCell.fieldName,
    //   valueCell,
    //   props.cell
    // )
    if (
      typeof props.cell === "string" ||
      typeof props.cell === "number" ||
      typeof props.cell === "boolean"
    ) {
      if (props.cell != valueCell) {
        // console.log("update valueCell:", valueCell, props.cell)
        setValueCell(props.cell)
      }
    } else {
      //truong hop con lai luon update valueCell
      setValueCell(props.cell)
    }
  }, [props.cell])

  // React.useEffect(() => {
  //   console.log("props change:", oneTableCell.fieldName, valueCell, props.cell)
  // }, [props])

  // if (typeof props.cell === "boolean") {
  //   console.warn("aaaaa", props.cell, valueCell, row)
  //   if (valueCell != props.cell) {
  //     setValueCell(props.cell)
  //   }
  // }

  // console.warn(
  //   "useConfigTableCell:",
  //   oneTableCell.fieldName,
  //   countForceUpdate,
  //   oneTableCell,
  //   isUpdating
  // )

  // console.log("render cell", fieldName)
  return {
    forceUpdate,
    fnList,
    updateValueCellFromRow,
    oneTableCell,
    extra: oneTableCell.extra,
    row: oneTableCell.row,
    fieldName: oneTableCell.fieldName,
    more: oneTableCell.more,
    moreObj: oneTableCell.more,
    canEdit: oneTableCell.canEdit,
    updateFieldData: (v, opts = {}) => {
      console.warn("updateFieldData:", v, oneTableCell)
      if (v != cell && !(cell == null && v == "")) {
        setIsUpdating(true)
        HUtils.runFuntion(oneTableCell.fnList, kfnRequestUpdate, [
          {
            ...opts,
            apiController: fnListMyPage.fnGetApiPath(fnList),
            fieldName: opts.fieldName || oneTableCell.fieldName,
            row: oneTableCell.row,
            newValue: v,
            cbSuccess: v => {
              console.warn("cbss", v)
              setIsUpdating(false)
              fnListMyPage.fnForceUpdateTable(fnList, [])
              if (opts && opts.cbSuccess) {
                opts.cbSuccess(v)
              }
              //bo sung cbSuccessUpdate danh cho cell ben trong cell
              if (props.fnList && props.fnList.cbSuccessUpdate) {
                props.fnList.cbSuccessUpdate()
              }
            },
            cbError: e => {
              console.warn("cber", e)
              setIsUpdating(false)
              if (opts && opts.cbError) {
                opts.cbError(v)
              }
            },
          },
        ])
      } else {
        console.warn("not update:", v, cell, opts)
        if (opts && opts.cbError) {
          opts.cbError({ msg: "Nothing to change" })
        }
      }
    },
    requestCustom: (opts = {}) => {
      console.log("requestCustom:", opts)
      HUtils.runFuntion(oneTableCell.fnList, kfnRequestCustom, [
        {
          ...opts,
        },
      ])
    },
    getSharedConfig: () => {
      return {}
    },
    getMoreAttrsFromCustomConfig: () => {
      if (
        MyTableCellHelpers["ConfigByTypes"] &&
        MyTableCellHelpers["ConfigByTypes"][oneTableCell.type] &&
        MyTableCellHelpers["ConfigByTypes"][oneTableCell.type].moreAttrs
      ) {
        return MyTableCellHelpers["ConfigByTypes"][oneTableCell.type].moreAttrs
      }
      return {}
    },
    getMorePropsButton: ({ defaultStyle } = {}) => {
      let _moreProps = {}
      let _style = defaultStyle || {}
      if (isFetching) {
        _moreProps.disabled = true
      }
      //bo sung style button from more
      if (more && more.button && more.button.style) {
        _style = { ..._style, ...more.button.style }
      }
      if (_style && Object.keys(_style).length > 0) {
        _moreProps.style = _style
      }
      return _moreProps
    },
    renderHide: () => {
      return <div className="disabled"></div>
    },
    isUpdating: isUpdating,
    setIsUpdating: v => {
      setIsUpdating(v)
    },
    isFetching,
    setIsFetching,
    valueCell,
    setValueCell,
    classNameWrap: (df = "", customConfig = {}) => {
      return cls(df, {
        disabled: oneTableCell.canEdit === false,
        "a-scrwhhover":
          more.scrwhhover != null
            ? more.scrwhhover
            : customConfig.scrwhhover || false,
        "styled-mini": more.styledMini,
        ...(props.custom != null && props.custom.className != null
          ? {
              [props.custom.className]: true,
            }
          : {}),
      })
    },
    styleWrap: (df = {}, overideStyle = {}) => {
      return V.Cell_GetStyleCell({
        extra,
        more,
        row,
        defaultStyle: df,
        overideStyle,
      })
    },
    // getSourceData: fnList.fnGetSourceData,
    onUpdateRow: newRow => {
      HUtils.runFuntion(oneTableCell.fnList, "onUpdateRow", [
        {
          row,
          newRow,
        },
      ])
    },
    getTextDisplaySelect: () => {
      // console.warn("getTextDisplaySelect:", valueCell)
      let _display = ""
      if (more.fDisplay && row[more.fDisplay]) {
        _display = row[more.fDisplay]
      } else if (more.fShow && row[more.fShow]) {
        _display = row[more.fShow]
      }
      if (_display == "" && valueCell != null && valueCell !== "") {
        let _sourceData = fnList.fnGetSourceData()
        // console.warn("sourceData:", _sourceData)
        let _textFromOption = HOptions.getTextDisplayOfValueFromOptions(
          _sourceData,
          valueCell
        )
        if (_textFromOption) {
          _display = _textFromOption
        }
      }
      let _alwayShowSelectValueIfNoText = true //can cau hinh
      if (_display == "" && _alwayShowSelectValueIfNoText) {
        _display = valueCell || ""
      }
      return _display
    },
    renderTopRight: (moreBtns = [], moreConfig = {}) => {
      return (
        <MyTableCell
          {...props}
          fnList={fnList}
          more={more}
          oneTableCell={oneTableCell}
          type="_barTopRight"
          moreBtns={moreBtns}
          moreConfig={moreConfig}
        />
      )
    },
    renderLoadingOverlay: () => {
      return (
        <div className="a-overlay-center">
          <MyLoading type="threedotsvertical" size={12} />
        </div>
      )
    },
    onClickForm: ({ customMore, cbSuccess, customShowForm } = {}) => {
      let _more = customMore || more
      console.warn("onClickForm:", _more)
      setIsFetching(true)
      FlowCheckApiShowModal({
        extra,
        row,
        more,
        fnList: fnList,
        cb: ({ canShow, config }) => {
          if (canShow) {
            const configForm = oneTableCell.getConfigForm({ customMore: _more })
            let _defaultValuesForm = row
            if (_more.fDefaultValues) {
              if (row[_more.fDefaultValues]) {
                _defaultValuesForm = row[_more.fDefaultValues]
              } else {
                _defaultValuesForm = {}
              }
            }
            if (configForm) {
              const configFormToShow = MyFormControlHelpers[
                "getConfigFormFromConfigV1"
              ](configForm, {
                defaultValues: _defaultValuesForm,
                fnList: {
                  ...fnList,
                  onSubmitForm: (valuesForm, { myForm }) => {
                    console.warn("onSubmitForm:", valuesForm)
                    const configApi = oneTableCell.getConfigApi({
                      customMore: _more,
                    })
                    return new Promise((resolve, reject) => {
                      if (configApi) {
                        configApi.query = Object.assign(
                          configApi.query,
                          valuesForm
                        )
                        apiWithConfigApi(configApi)
                          .then(res => {
                            console.warn("onClickBtn API rs", res)
                            FlowParseResponseAndNeedReload({
                              extra,
                              row,
                              fieldName,
                              more,
                              oneTableCell: oneTableCell,
                              response: res,
                            })
                            if (myForm.closeMyModal) {
                              myForm.closeMyModal()
                            }
                            cbSuccess && cbSuccess(res)
                            resolve(res)
                          })
                          .catch(error => {
                            reject(error)
                          })
                      } else {
                        console.warn("Missing configApi")
                        reject("Missing configApi")
                      }
                    })
                  },
                },
              })
              const configModal = oneTableCell.getConfigModal({
                customMore: _more,
              })
              if (customShowForm) {
                customShowForm(configFormToShow, { ...configModal })
              } else {
                MyModal.showFromMyForm(configFormToShow, { ...configModal })
              }
            } else {
              console.error("Missing configForm:", configForm)
            }
          }
          setIsFetching(false)
        },
      })
    },
    onClickApi: ({
      customMore,
      cbSuccess,
      cbError,
      cbBeforeRequest,
      cbAfterRequest,
    } = {}) => {
      let _more = customMore || more
      console.warn("onClickApi:", _more)
      const configApi = oneTableCell.getConfigApi({ customMore: _more })
      if (_more.confirmMsg) {
        MyModal.showConfirm({
          title: _more.titleConfirm || "Confirm",
          msg: _more.confirmMsg,
          onClickOK: cModal => {
            if (configApi) {
              if (cbBeforeRequest) {
                cbBeforeRequest()
              }
              apiWithConfigApi(configApi)
                .then(res => {
                  console.warn("onClickBtn API rs", res)
                  FlowParseResponseAndNeedReload({
                    extra,
                    row,
                    fieldName,
                    more: _more,
                    oneTableCell: oneTableCell,
                    response: res,
                  })
                })
                .finally(() => {
                  if (cbAfterRequest) {
                    cbAfterRequest()
                  }
                })
            } else {
              console.warn("Missing configApi")
            }
            if (cModal && cModal.hide) {
              cModal.hide()
            }
          },
        })
      } else {
        if (configApi) {
          if (cbBeforeRequest) {
            cbBeforeRequest()
          }
          apiWithConfigApi(configApi)
            .then(res => {
              console.warn("onClickBtn API rs", res)
              FlowParseResponseAndNeedReload({
                extra,
                row,
                fieldName,
                more: _more,
                oneTableCell: oneTableCell,
                response: res,
              })
            })
            .finally(() => {
              if (cbAfterRequest) {
                cbAfterRequest()
              }
            })
        } else {
          console.warn("Missing configApi")
          if (cbError) {
            cbError()
          }
        }
      }
    },
    onClickFiles: ({ ctc, customMore, canUpload } = {}) => {
      let _more = customMore || more
      console.warn("onClickFiles:", _more)
      MyModal.showFromComponent(
        propsComponent => {
          return (
            <ModalContentFiles
              {...propsComponent}
              more={_more}
              canUpload={canUpload}
              ctc={ctc}
            />
          )
        },
        {
          title: "Modal",
          backdrop: "static",
          size: "xl",
          showHeader: false,
          fnList: {
            updateCell: (v, opts) => {
              console.warn("Updatecell:", v, opts)
            },
            fnGetApiPath: () => {
              return fnListMyPage.fnGetApiPath(fnList)
            },
            onChangeData: data => {
              console.warn("onChangeData:", data, cell)
              if (data && cell) {
                //server hien tra ve Cell.Id = null nen de bo sung them, chi can chua Photos
                if (
                  data.Id === cell.Id ||
                  (cell.hasOwnProperty &&
                    data.hasOwnProperty &&
                    cell.hasOwnProperty("Photos") &&
                    data.hasOwnProperty("Photos"))
                ) {
                  HUtils.mergeObjIfExist(row[fieldName], data)
                  HUtils.runFuntion(oneTableCell.fnList, "onUpdateRow", [
                    {
                      row,
                      newRow: row,
                    },
                  ])
                  forceUpdate()
                } else {
                  console.warn("Update data row, wrong ID", data, cell)
                }
              } else {
                console.warn("Update data row, wrong data", data, cell)
              }
            },
          },
        }
      )
    },
  }
}

export { fnListOfTableCell, helpersOfTableCell }
export default useConfigTableCell
