import { HUtils, MyUI, MyTableCellHelpers, HJson } from "@macashipo/mlib"
import { Tooltip } from "src/core/components"
import {
  kfnGetSourceData,
  kfnRenderBadge,
  kfnGetHeaderTitle,
  fnListMyPage,
  kfnGetConfigInMore,
  kfnGetConfigModal,
  kfnGetIsHide,
} from "src/helpers/fnList"
import * as AHOptions from "src/core/helpers/options"
import V from "src/helpers/V"
import { FlowGetCanShow, KMore } from "src/helpers/Flows/TableCell"
const kfnGetScreenCode = "fnGetScreenCode"

const fnListOfTableCell = {
  init({ oneTableCell }) {
    let _fnList = {
      ...oneTableCell.fnList,
    }
    _fnList[kfnGetSourceData] = () => {
      const { row, extra } = oneTableCell
      let _optionKey = oneTableCell.extra.Type.source
      let _sourceList = fnListMyPage.getSourceList(_fnList)
      // console.warn("_sourceList", _sourceList, fieldName, extra)
      if (AHOptions.isHasLevelOptionKey(_optionKey)) {
        return AHOptions.getOptionsByLevel(
          _sourceList,
          _optionKey,
          row,
          extra.Type.sourceField
        )
      } else if (_optionKey && _optionKey.startsWith("[")) {
        //bo sung truong hop optionKey la string json source
        return HJson.getObj(_optionKey)
      } else if (_optionKey && _optionKey.startsWith("f")) {
        let _optionField = _optionKey.slice(1, _optionKey.length)
        if (row[_optionField] != null && Array.isArray(row[_optionField])) {
          return row[_optionField]
        } else if (row[_optionKey] != null && Array.isArray(row[_optionKey])) {
          return row[_optionKey]
        }
        return []
      } else {
        return _sourceList[_optionKey] || []
      }
    }
    _fnList[kfnRenderBadge] = ({ customMore, count, tooltip } = {}) => {
      // console.info(kfnRenderBadge, count, tooltip, more, row)
      const { row, more } = oneTableCell
      return helpersOfTableCell.renderBadge({
        more: customMore || more,
        row,
        count,
        tooltip,
      })
    }

    _fnList[kfnGetHeaderTitle] = ({ customTitle } = {}) => {
      // console.warn("get Header title", row, oneTableCell.row)
      return V.Cell_GetHeaderTitle({ extra: oneTableCell.extra, customTitle })
    }

    _fnList[kfnGetConfigInMore] = (key = "", df) => {
      const { more } = oneTableCell
      let _result = df
      if (more && more.hasOwnProperty(key)) {
        _result = more[key]
      }
      return _result
    }
    _fnList[kfnGetConfigModal] = () => {
      const { row, extra, more } = oneTableCell
      return V.Cell_GetConfigModal({
        extra,
        more,
        row,
      })
    }
    _fnList[kfnGetIsHide] = () => {
      const { row, extra, more } = oneTableCell
      const cell = oneTableCell.getValue()
      //check canshow
      let _canShow = FlowGetCanShow({ extra, row, more })
      // console.log("_canShow", _canShow, oneTableCell.fieldName)
      if (_canShow === false) {
        return true
      }
      let _isHide = false
      if (
        more[KMore.hideWhenEmpty] &&
        (cell === "" || cell == null || cell === 0)
      ) {
        return true
      }
      if (more[KMore.hideWhenNull] && cell == null) {
        return true
      }
      if (
        MyTableCellHelpers["ConfigByTypes"] &&
        MyTableCellHelpers["ConfigByTypes"][oneTableCell.type] &&
        MyTableCellHelpers["ConfigByTypes"][oneTableCell.type]["isHide"]
      ) {
        if (
          MyTableCellHelpers["ConfigByTypes"][oneTableCell.type]["isHide"](
            oneTableCell
          )
        ) {
          return true
        }
      }
      return _isHide
    }

    _fnList.onSendMsg = msg => {
      console.error("missing onSendMsg, need update fnList", msg)
    }
    _fnList.onReExpand = () => {
      const { row } = oneTableCell
      HUtils.runFuntion(row, "_fnForceUpdateDetail")
    }

    //update fnList to oneTableCell again
    oneTableCell.fnList = _fnList
    return _fnList
  },
  fnGetScreenCode(ctc = {}, args = []) {
    return HUtils.runFuntion(ctc.fnList, kfnGetScreenCode, args)
  },
}

const helpersOfTableCell = {
  renderBadge({ more = {}, row = {}, count, tooltip } = {}) {
    const configBadge = more.configBadge || more.badge
    // console.warn("renderBadge", more, count, row)
    if (count != null || (configBadge && configBadge.show === true)) {
      let _count = count
      let _tooltip = tooltip
      if (
        configBadge &&
        configBadge.fCount &&
        row[configBadge.fCount] != null
      ) {
        _count = row[configBadge.fCount]
      }
      if (_count) {
        if (configBadge && configBadge.fTooltip && row[configBadge.fTooltip]) {
          _tooltip = row[configBadge.fTooltip]
        }
        if (_tooltip) {
          return (
            <Tooltip overlay={<MyUI type="html" html={_tooltip} />}>
              <span className="mct-ui-badge">{_count}</span>
            </Tooltip>
          )
        }
        return <span className="mct-ui-badge">{_count}</span>
      }
    }
  },
}
export { helpersOfTableCell, fnListOfTableCell }
